.App {
  text-align: left;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 60px;
}

.App-header {
  background-color: #222;
  height: 100px;
  padding: 20px;
  color: white;
  text-align: left;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1em;
  font-weight: bold;
}

.App-title {
  font-size: 1.5em;
}

.card > div {
  text-align: center;
}

.App-content {
  padding-top: 20px;
  font-size: large;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.search {
  padding: 5px 0 20px 0;
}

tr {
  color: #5b5757;
}

.highlighted {
  border-style: groove;
}

.results {
  padding-top: 15px;
}

.restaurant-section {
  margin: 20px auto;
}

.footer {
  padding-top: 20px;
  text-align: center;
}

.footer-items {
  list-style-type: none;
}

.card {
  width: 18rem;
}
